.contactform{
    align-items: center;
    background-color: #0E1317;
    display: flex;
    flex-direction:row;
    height: 700px;
    gap:10px;
    justify-content: center;
}
.Form-img{
    width: 40%;
    height:80%;
}
.glass{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width:45%;
    height: 80%;
    background: linear-gradient(135deg, rgba(134, 110, 110, 0.1), rgba(196, 178, 178, 0));
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter:blur(10px);
    box-shadow: 0 8px 32px 0 rgba(0,0,0,0.37);
    gap: 40px; 
}
.text{
    display: flex;
    flex-direction: column;
    width:85%;
    gap: 30px;
}
.Contacth21a{
    margin: 0;
    padding: 0;
    color:#c19977;
    font-size: 45px;
    background: transparent;
}
.text111{
    width: 100%;
    padding:8px 12px;
    color:black;
    font-size: 18px;
    border: 2px solid #c19977;
}
.form-errora1 {
    color:#c19977;
    font-size: 14px;
    margin: 0;
}

.Contactbtn1A{
    width: 100%;
    padding:7px 12px;
    color:white;
    font-size: 18px;
    border: none;
    font-weight: 500;
    background-color: #c19977;
    margin-bottom: 5px;
}
.Contactbtn1A:hover{
    background-color: #222d3b;
}
.Cimg-1{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media(max-width:850px){
    .contactform{
        flex-direction: column;
        height: 100%;
        padding: 30px 0px;
    }
    .Form-img,.glass{
        width: 90%;
    }
}