.homeimg1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: beige;
    background:rgba(0,0,0,0.6)  url('../../assets/Homeimg/Coffe-HI1.jpg')  no-repeat center ;
    background-size: cover;
    background-blend-mode:darken ;
    height: 400px;
}

.homeimg1 .pHI1{
    font-size: 18px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 600;
    align-self: center;
    color: #C19977;
    letter-spacing: 1px;
}
.homeimg1 .pHI2{
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size:55px;
    align-self: center;
}
.homeimg1 .pHI3{
    padding: 0 20px;
    font-size: 19px;
    font-family: Oswald,serif;
    text-align: center;
    letter-spacing:0.5px;
}
@media (max-width: 600px) {
    .homeimg1 .pHI3{
        font-size: 16px;
    }
    .homeimg1 .pHI2{
      font-size: 50px;
    }
  }


/* .......................................Homesec4 css......................... */

.Homesec4A {
    padding: 40px 80px 20px 80px;
  }
  
  .Homesec4main {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    row-gap: 40px;
    column-gap: 20px;
    color: white;
    padding: 40px 10px;
    justify-items: center;
  }
  
  .Homesec4items1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 420px;
  }
  .Homesec4items1A {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 420px;
  }
  .Homesec4items2 {
    display: flex;
    gap: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .Homesec4imgA {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .Hsec4h6a {
    color: #c19977;
    font-size: 18px;
    letter-spacing: 3px;
    align-self: center;
  }
  .Hsec4h3b {
    color: black;
    font-size: 28px;
    letter-spacing: 3px;
    align-self: center;
  }
  .Hsec4pa {
    text-align: center;
    color: black;
    font-size: 14px;
    letter-spacing: 0.8px;
    padding: 0 30px;
  }
  
  .Hsec4btn1 {
    width: 130px;
    color: white;
    height: 42px;
    font-size: 18px;
    margin: 10px 10px;
    border: 1px solid #c19977;
    border-radius: 3x;
    background-color: #c19977;
    font-weight: 500;
  }
  .Hsec4btn1 a{
    text-decoration: none;
    color: white;
  }
  
  @media (max-width: 900px) {
    .Homesec4A {
      padding: 40px 30px;
    }
    .Homesec4main {
      column-gap: 10px;
    }
  }
  @media (max-width: 900px) {
    .Homesec4A {
      padding: 30px 20px;
    }
    .Homesec4main {
      grid-template-columns: 2fr 1fr;
      column-gap: 0;
    }
    .Hsec4h3b {
      font-size: 23px;
    }
    .Homesec4items1 {
      display: none;
    }
  }
  @media (max-width: 580px) {
    .Homesec4A {
      padding: 20px 10px;
    }
    .Hsec4h3b {
      font-size: 20px;
    }
    .Homesec4main {
      grid-template-columns: 1fr;
    }
    .Homesec4items1A {
      width: 85%;
    }
  }
  


/* home slider csss  */

.hs img{
  height: 600px;
  width: 100%;
}
.hs{
  z-index: 1;
}
.hs .p1{
  height: 500px;
  width: 100%;
  background: rgba(0,0,0,.5) url('../../assets/Homeimg/h-slider2.png') no-repeat center ;
  background-size: cover;
  background-blend-mode: darken;
  color: #ffb300;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.p1 h1{  
  animation: slide-down 5s;
  position: absolute;
}
@keyframes slide-down {
  from{
      opacity: .2;
      line-height: 50px;
      top: 43%;
      
