.footermain{
    width: 100%;
    background-color: white;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* border: 1px solid blue; */
}
.Fcontain{
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 20px;
    column-gap: 20px;
    padding: 40px 20px 20px 20px;
}
.Fa1{
    color:black;
    width:100%;
    height: 300px;
    border-bottom: 2px dashed black;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.lastfooter{
    height: 60px;
    width: 100%;
    background-color: #111;
    display: flex;
    align-items: center;
    justify-content: center;
}
.pF1a{
    font-size: 15px;
    color: white;
    margin: 0;
}
.Fa1h4{
    color:#111;
    font-family: Oswald,serif;
    font-size: 24px;
    line-height: 1.78em;
    font-weight: 700;
    padding: 8px 0 5px 10px;
}
.Fpa1{
    color: #111;
    font-family: "Roboto Slab",sans-serif;
    font-size: 16px;
    padding: 0 8px 17px 10px;
    margin: 0;
}
.Fbtna1{
    width: 130px;
    height: 42px;
    font-size: 18px;
    margin: 10px 10px;
    color: white;
    border: 1px solid white;
    border-radius: 3x;
    background-color:#C19977;
    font-weight: 500;
}
.Fbtna1 a{
    padding: 0px;
    margin: 0px;
    color: white;
    text-decoration: none;
}

.Fpa1 a{
    text-decoration: none;
    color: #111;
}
.Ficonlist{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap:25px;
    padding: 15px;
}
.listicona{
    background-color: white;
    color: #C19977;
    height: 32px;
    width: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border-radius: 50%; */
    cursor: pointer;
}
.listicona:hover{
    background-color:#C19977;
    color: white;
}
@media(max-width:1050px){
    .Fcontain{
        grid-template-columns: 1fr 1fr;
    }
    .Fa1{
        height: auto;
    }
}
@media(max-width:650px){
    .Fcontain{
        grid-template-columns: 1fr;
    }
    .Fa1{
        height: auto;
    }
}
@media(max-width:385px){
    .pF1a{
        font-size: 12px;
    }
}