
.mynav-menu{
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: center;
    text-align: center;
    list-style: none;
    gap: 15px;
    height: 100%;
    background-color: white;
}
.Drawerh4a{
    background-color: #C19977;
    text-align: center;
    padding: 15px 20px;
    font-size: 35px;
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 0 0 20px 0;
    border-radius: 0 0 50% 50%;
}
.mynav-menu li{
    display: flex;
    border-radius:0 50% 50% 0 ;
    width: 90%;
    background-color: #C19977;
}
.drawer{
    z-index: -1;
}
.mynav-menu li a{
    display: flex;
    justify-content:flex-start;
    align-items: center;
    gap: 30px;
    font-size: 20px;
    text-decoration: none;
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    width: 100%;
    padding: 10px 20px;
    margin: 0;
}
.mynav-menu li:hover{
    background-color:#C19977;
}

@media screen and (min-width: 850px) {
    .dr {
        display: none;
    }
}