.CartaddA1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 60px ;
  gap: 30px;
}
.CartitemaddA2 {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content:space-between;
  border: 2px solid black;
  padding: 15px;
  
}
.Cartaddimg1 {
  height: 120px;
  width: 140px;
}
.carttitle1{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 80px;
}
.cartadd-title {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 80px;
}
.CAh4 {
  margin: 0;
  text-align: center;
  width: 200px;
  font-size: 17px;
  font-weight: 600;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.CAp1 {
  margin: 0;
  width: 100px;
  font-size: 18px;
  font-weight: 500;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.cartadd-count1 {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
}
.cartcont2 {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  width: 60px;
  height: 50px;
}
.CAp2 {
  margin: 0;
  font-size: 19px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 500;
}
.cartadd-count2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
}
.CAmainp {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  width: 24px;
  border: 2px solid black;
}
.CAp3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}
.CAp4 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}
.cart-total1{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.CAh4A1{
    margin: 0;
    font-size: 18px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.CAp5{
    margin: 0;
    font-size: 18px;
    font-weight: 500;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.cart-delete{
    display: flex;
    width: 50px;
    height: 120px;
    align-items: center;
    justify-content: center;
    border:2px solid black;
}
.CAh4A2{
    margin: 0;
    font-size: 20px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}


@media(max-width:1000px){
  .cartadd-title{
      gap: 15px;
  }
  .carttitle1{
    gap: 30px;
  }
  
}
@media(max-width:890px){
  .cartadd-title{
      flex-direction: column;
  }
  .CAp1{
    width: 200px;
    text-align: center;
  }
  .CAh4{
    border-bottom: 1px solid black;
    padding-bottom: 3px;
  }
}
@media(max-width:760px){
  .CartaddA1{
    padding: 35px 35px;
  }
  .carttitle1{
      flex-direction: column;
      gap: 10px;
  }
  .cartadd-title{
    gap:8px;
}
.CAh4{
  font-size: 16px;
  width: 160px;
  text-align: center;
}
  .CAp1{
    font-size: 14px;
    width: 160px;
    font-weight: 550;
