.contactimg1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: beige;
    background:rgba(0,0,0,0.5)  url('../../assets/Contactimg/contact_bg1.jpg')  no-repeat center ;
    background-size: cover;
    background-blend-mode:darken ;
    height: 400px;
}

.contactimg1 .pCI1{
    font-size:55px;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.contactimg1 .pCI2{
    font-family: Oswald,serif;
    font-size:22px;
    padding: 0 30px;
    text-align: center;
}
@media (max-width: 600px) {
    .contactimg1 .pCI1{
        font-size:45px;
    }
    .contactimg1 .pCI2{
      font-size: 18px;
    }
  }


/* .............................Contact details................................................. */

.c2a1{
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 30px; */
    flex-direction: column;
    gap: 20px;
    padding-bottom: 30px;

}
.c2{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height:250px ;
    width: 85%;
    gap: 25px;
    padding-top:20px;
}
.c2 .c2-cards{
    box-shadow: #c19977 0px 5px 15px;
    border-radius: 10px;
    width: 100%;
    height: 180px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
 }
.c2-cards h4{
    font-size: 19px;
}
.c2-cards h6{
    font-size: 14px;
}
 .c-iconbox{
    border: 3px solid #c19977;
    background-color:white;
    border-radius: 50%;
    height: 80px;
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    
 }
.c-icon{
    position: absolute;
    color: #c19977;
}


@media(max-width: 950px){
    .c2{
        flex-wrap: wrap;
    }   
}