/* .....................................login........................... */
.l1{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 600px;
    background: rgba(0,0,0,.5) url('../../assets/title-img-1.jpg') no-repeat center ;
    background-size: cover;
}
.l1 h1{
   margin: 25px;
}
.loginf{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 40%;
}
/* .........................first div....................... */

.loginf .tfield{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    width: 100%;
    padding: 10px;
    background-color: white;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.emailpass{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    flex-direction: column;
    width: 100%;
}
.tfield .t1{
    width: 80%;
}
.tfield a{
    text-decoration: none;
    color:blue;
}
.loginbtn{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0px;
    width: 80%;
    background-color: #C19977;
    border-radius: 5px;
    font-size: 20px;
    font-weight: 500;
    color: white;
    border:#C19977;
    opacity: 1;
    letter-spacing:0.4px;
    margin:18px 0 12px 0;
}
.loginbtn:hover{
  opacity: .9;
}
.googlebtn{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 11px 0px;
    width: 80%;
    font-size: 18px;
    font-weight: 500;
    border: 2px solid black;
    border-radius: 5px;
    color: black;
    opacity: 1;
    letter-spacing:0.3px;
}
.googlebtn:hover{
    opacity: .9;
}
.googlebtn img{
    height:80%;
    width: 6%;
}
.tfield .google{
    width: 80%;
    display: flex;
    align-items: center;
    gap: 10px;
}
.Lp1{
    margin: 0px;
    font-size: 17px;
    letter-spacing:0.3px;
}
.google img{
    margin-left: 15px;
    height:80%;
    width: 9%;
}
.gbtn{
    width: 70%;
}
@media(max-width: 1024px){
    .loginf{
        
        width: 52%;
    }
    .emailpass{
        width: 100%;
    }
}

@media(max-width: 800px){
    .loginf{
        width: 65%;
    }
    .emailpass{
        width: 100%;
    }
    .tfield .t1{
        width: 85%;
    }
    .loginbtn ,
    .googlebtn{
        width: 85%;
    }
}
@media(max-width: 550px){
    .loginf{
        width: 88%;
    }
    .emailpass{
        width: 100%;
    }
    .tfield .t1{
        width: 90%;
    }
    .loginbtn ,
    .googlebtn{
        width: 90%;
    }
    .googlebtn img{
        width: 7%;
    }
}
