    color: beige;
    background:rgba(0,0,0,0.3)  url('../../assets/Serviceimg/a-bg-3.jpg')  no-repeat center ;
    background-size: cover;
    background-blend-mode:darken ;
    height: 400px;
}

.Sp1{
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-weight: 500;
    font-size:50px;
}
