/* ...................................Servicesec2 css ................................ */

.Servicesec2a {
    background: rgba(0, 0, 0, 0.1) url("../../assets/Serviceimg/a-bg-3.jpg")
      no-repeat center;
    background-size: cover;
    background-blend-mode: darken;
    padding: 60px 50px;
  }
  
  .servicesec2A1 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 50px;
    background-color: #f9f9f9;
    column-gap: 20px;
    color: black;
    padding: 80px 10px;
  }
  
  .servicesec2item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .servicesec2item .servicesec2-icons {
    margin-bottom: 20px;
    padding: 2px;
    color: black;
    border: 2px solid black;
    border-radius: 50%;
    height: 60px;
    width: 60px;
  }
  .servicesec2h1a {
    font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
    font-size: 19px;
    /* color: #C19977; */
  }
  .servicesec2pa1 {
    padding: 5px 15px;
    text-align: center;
    font-family: "Roboto Slab", sans-serif;
    font-size: 15px;
    line-height: 25px;
    color: #707070;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
  }
  
  @media (max-width: 1045px) {
    .servicesec2pa1 {
      padding: 5px 8px;
    }
  }
  @media (max-width: 880px) {
    .servicesec2A1 {
      grid-template-columns: 1fr 1fr;
    }
  }
  @media (max-width: 580px) {
    .Servicesec2a {
      padding: 45px 25px;
    }
    .servicesec2A1 {
      grid-template-columns: 1fr;
      row-gap: 0;
      padding: 0 20px;
    }
    .servicesec2pa1 {
      padding: 3px 8px;
    }
    .servicesec2item {
      padding: 35px 0;
      border-bottom: 1px solid black;
    }
  }
  

