.cardapi1a{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 20px;
    background:rgba(0,0,0,0.2)  url('../../assets/Extraimg/Ebg-img-2.png')  no-repeat center ;
    background-size: cover;
    background-blend-mode:darken ;
}
.cardapih6a{
     font-size: 20px;
    font-weight: 500;
    text-align: center;
    color: black;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    letter-spacing: 0.9px;
}
.cardapih6b{
     font-size: 30px;
    font-weight: 500;
    text-align: center;
    color: black;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    letter-spacing: 0.9px;
}
.grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-content: center;
    justify-content: center;
    padding: 20px;
    column-gap: 35px;
    row-gap: 35px;
}
.p1a{
    font-size: 16px;
    font-weight: 550;
    padding: 10px 0 5px 0;
    margin:0px;
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.p2a{
    padding: 0 0 5px 0;
    font-size: 16px;
    font-weight: 550;
    margin:0px;
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.cards {
  border: 1px solid rgba(102, 78, 78, 0.18);
  box-shadow: 0 10px 32px 0 rgba(32, 32, 32, 0.37);
}
.cards:hover{
    cursor: pointer;
    transform: scale(1.03);
    transition: .3s ease-in-out;
}
.Cardimg1a{
    height: 190px;
    cursor: pointer;
    
}
.Cardimg1a:hover{
   
    transform: scale(0.9);
    transition: 0.3s ease-in-out;
}
.btna1{
    justify-content: center;
    cursor: pointer;
}
.Cartbtn2A{
    width:100%;
    background-color: #Ca9977;
    border: 1px solid #Ca9977;
    border-radius: 100%  100%  0  0;
    height: 40px;
    font-size: 15px;
    color: beige;
    font-weight: 500;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

@media(max-width:1024px){
    .grid{
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 20px;
    }
}

@media(max-width:680px){
    .grid{
        grid-template-columns: 1fr 1fr ;
        column-gap: 10px;
        padding:20px 8px;
    }
    .Cardimg1a{
        height: 180px;
    }
    .cardapih6a{
        font-size: 20px;
    }
}
@media(max-width:480px){
    .p1a{
        font-size: 15px;
        font-weight: 600;
    }
    .p2a{
        font-size: 14px;
    }
}