.Mlist1a{
    padding:35px 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}
.Mlist1b{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 45px;
    row-gap: 25px;
}
.Ml1a{
    height: 400px;
    width: 350px;
    background-color: white;
}
.Mlistimg{
    height: 250px;
    width:100%;
}
.Mlimg1a{
    object-fit: cover;
    height:100%;
    width: 100%;
}
.Mlh4a{
    color:#111;
    font-family: Oswald,serif;
    font-size: 18px;
    line-height: 1.78em;
    font-weight: 700;
    padding: 8px 0 5px 10px;
}
.Mlpa{
    color: #111;
    line-height: 27px;
    font-family: "Roboto Slab",sans-serif;
    font-size: 15px;
    padding: 0 0 8px 10px;
} 


@media(max-width:1150px){
    .Mlist1b{
        grid-template-columns: 1fr 1fr;
    }
}
@media(max-width:770px){
    .Mlist1b{
        grid-template-columns: 1fr;
    }
    .Ml1a{
        width:500px;
    }
    .Mlistimg{
        height: 300px;
    }
}
@media(max-width:525px){
    .Ml1a{
        width:320px;
    }  
    .Mlistimg{
        height:250px;
    }
}