.register{
    background: rgba(0,0,0,.5) url('../../assets/title-img-1.jpg') no-repeat center ;
    background-size: cover;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.arrowback{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: flex-start;
    font-size: 25px;
    font-weight: 700;
    padding-left: 10px;
    color: black;
}
.arrowback a{
    padding: 0px;
    margin: 0px;
    color: black;
}
.RF-h1a1{
    font-size: 37px;
    color:black;
    font-weight: 600;
    margin:-10px 0 15px 0 ;
}
.Rform{
    width: 40%;
    background-color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    border: 1px solid rgba(255,255,255,0.18);
    border-radius: 15px;
    margin:3% 0;
}
.Rform .textR{
    display: flex;
    flex-direction: column;
    width:80%;
}
.Rform .textR1{
    width: 100%;
    margin: 20px 0px 8px 0px; 
}
.textR1 label {
    font-size:17px;
}

.Rbtn1{
    height:50px;
    width: 80%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #C19977;
    border:#C19977;
    border-radius: 5px;
    margin: 15px 0 25px 0;
}
.Rbtn2{
    background: transparent;
    border: none;
    font-size: 20px;
    font-weight: 500;
    width: 100%;
    color: white;
}


@media screen and (max-width:1150px) {
    .Rform{
        width: 50%;
    }
}
@media screen and (max-width:850px) {
    .Rform{
        width: 65%;
    }
}

@media screen and (max-width:700px) {
    .Rform{
        width: 75%;
    }
    .RF-h1a1{
        font-size: 28px;
    }
}
@media screen and (max-width:600px) {
    .Rform{
        width: 88%;
    }
    .Rform .textR{
        width: 85%;
    }
    .Rbtn1{
        width: 85%;
    }
}