.Aboutbg-1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: beige;
    background:rgba(0,0,0,0.3)  url('../../assets/Aboutimg/A-bg-1.jpg')  no-repeat center ;
    background-size: cover;
    background-blend-mode:darken ;
    height: 400px;
}

.Ap1{
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size:55px;
}
.Ap2{
    font-family: Oswald,serif;
    font-size:20px;
    text-align: center;
    padding: 0 30px;
}
@media (max-width: 600px) {
  .Ap1{
      font-size:45px;
  }
  .Ap2{
    font-size: 18px;
  }
}

/* .................................Acountingsec1 css..................................................... */
.ACountingsec {
    background-color: #f9f9f9;
  }
  
  .Countingsec1 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 50px;
    column-gap: 20px;
    color: white;
    background: rgba(0, 0, 0, 0.1) url("../../assets/Aboutimg/A-bg-2.jpg")
      no-repeat center;
    background-size: cover;
    background-blend-mode: darken;
    padding: 80px 10px;
  }
  
  .Acountitem1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .Acountitem1 .about-icons {
    margin-bottom: 20px;
    /* padding: 15px; */
    color:white;
    /* border: 2px solid white; */
    /* border-radius: 50%; */
    height: 75px;
    width: 75px;
  }
  .Countingsech1{
      font-size: 44px;
      /* color: #C19977; */
  }
  .Countingsech4{
      font-size: 20px;
  }
  
  
  @media (max-width: 850px) {
      .Countingsec1 {
        grid-template-columns: 1fr 1fr;
      }
    }
  @media (max-width: 520px) {
      .Countingsec1 {
        grid-template-columns: 1fr;
        row-gap:0;
        padding: 0 20px;
      }
      .Acountitem1{
        padding: 45px 0;
        border-bottom: 1px solid white;
      }
  }
  