.Bookingbg-1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: beige;
    background:rgba(0,0,0,0.6)  url('../../assets/Extraimg/tablebookbg-img1.jpg')  no-repeat center ;
    background-size: cover;
    background-blend-mode:darken ;
    height: 450px;
}

.BTp1{
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size:50px;
    letter-spacing: 2px;
}
.BTp1a{
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size:34px;
    color: #C19977;
    letter-spacing: 0.8px;
}
.BTp2{
    font-family: Oswald,serif;
    font-size:20px;
    text-align: center;
    padding: 0 25px;
    letter-spacing: 0.5px;
}

/* .................................Booktable css....................................................... */

.form-container {
    display: grid;
    grid-template-columns: 1fr;
}

.form-righta1 {
    display: flex;
    flex-direction: column;
    background: rgba(0, 0, 0, 0) url('../../assets/Extraimg/Ebg-img-2.png') no-repeat center;
    background-size: cover;
    background-blend-mode: darken;
    color: black;
    padding: 30px 100px;
}

.bth4{
    color:#C19977;
    /* padding-left: 20px; */
    align-self: center;
}

.bth2{
    text-align: start;
    align-self: center;
    /* padding-left: 20px; */
}

.mainform {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 30px;
    row-gap: 30px;
    padding: 20px 20px 20px 20px;
}

span label{
    font-size: 17px;
    font-weight: 500;
}
span input {
    width: 100%;
    padding: 10px;
    font-size: 15px;
    font-weight: 500;
}

span select {
    width: 100%;
    padding: 12px;
    font-size: 15px;
    font-weight: 500;
}
span select option{
    background-color: white;
}

option:checked{
    background-color: #C19977;
}
option:default{
    background-color: #C19977;
}

.form-error {
    color: #C19977;
    font-size: 13px;
    padding-left: 3px;
    margin: 0;
}

.fsubmit-btn {
    background-color: #C19977;
    border: none;
    color: white;
    font-size: 18px;
    font-weight: 600;
    padding: 10px;
    letter-spacing: 0.4px   ;
}

.fsubmit-btn:hover {
    background-color: black;
    color:white;
}



@media (max-width:1200px) {

    .mainform {
        grid-template-columns: 1fr 1fr;

    }

}

@media (max-width:850px) {

    .form-righta1 {
        padding: 30px 70px;
    }

}

@media (max-width:650px) {

    .form-righta1{
        padding: 30px 40px;
    }

}

@media (max-width:550px) {

    .mainform {
        grid-template-columns: 1fr;

    }

}

@media (max-width:420px) {

    .form-righta1 {
        padding: 30px 20px;
    }

    span input {
        padding: 7px;

    }

    span select {
        padding: 7px;

    }

    .fsubmit-btn {
        padding: 7px;
    }

}