.navbar{
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,.5) url('../../assets/title-img-1.jpg') no-repeat center ;
  background-size: cover;
  height: 70px;
  color: black;
  padding: 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  width: 100%;
  padding: 0 20px;

}
.navbar .logo{
  flex:3;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 40px;
  padding-left: 20px;
}
.logo h5{
  margin: 0;
  /* color:black ; */
  color: white;
  font-weight: 700;
}
.navbar .list{
  flex: 8;
  display: flex;
  justify-content:flex-start;
  align-items: center;
  gap: 20px;
  padding-left: 50px;
  list-style: none;
  font-size: 17px;
}
.list .bookbtn{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 7px 12px;
  margin-left: 10px;
  background-color: #C19977;
  color: white;
}
.list .bookbtn:hover{
  background-color: white;
  color: #C19977;
}

.bookbtn  a{
  font-weight: bolder;
}

.navbar li a{
  
  text-decoration: none;
  color: white;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;

}
.navbar .l a{
  padding: 5px 10px;
}
.navbar .l a:hover{
  background-color: #C19977;
  border-radius: 20px;
}

.navbar .icon{
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
  padding-right: 7px;
}

.icon .i1{
  padding-bottom: 3px;
  color: white;
  font-size: 25px;
}
.icon .i1 a{
  color: white;
}
.icon .i2{
  font-size: 25px;
  padding-bottom: 3px;
}
.icon .i2 a{
  color: white;
}
.icon .i2 a:hover{
  color: #C19977;
}
.icon .i3{
  margin: 0;
  font-size: 30px;
  display: none;
  color: white;

}

@media screen and (max-width: 1024px) {
  .navbar .list{
      justify-content: center;
      padding: 0;
  }
}
@media screen and (max-width:1070px) {

  .navbar .list{
    gap:9px;
  }
}

@media screen and (max-width: 990px) {

  .icon .i3{
    display: block;
  }
  .icon .i2{
    display: none;
  }

  .navbar .list{
      display: none;
  }

  .navbar .icon{
      padding-right: 10px;
  }

}
@media screen and (max-width:450){
  .navbar .logo{
      padding-left: 10px ;
  } 
}
