.Asec2A1{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
}
.Asec2A2{
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    padding: 10px;
}
.Asec2A3{
    height: 400px;
    padding:18px 60px;
}
.Asec2img1{
    width: 100%;
    height: 100%;
    border-radius: 18px;
    object-fit: cover;
}
.Asec2A4{
    height: 400px;
    display: flex;
    align-items:flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 16px;
    padding: 10px 31px;
}
.Asec2h3{
    font-size: 32px;
    color: #111;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 750;
}
.Asec2p1{
    font-family: "Roboto Slab",sans-serif;
    font-size: 17px;
    line-height: 30px;
    color: #707070;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    text-align: justify;
}
.Asec2btn1{
    width: 130px;
    height: 45px;
    background-color: #111;
    color:white ;
    font-size: 15px;
    font-weight: 500;
    border: 1px solid black;
    border-radius:25px ;
}
.Asec2btn1 a{
    padding: 0px;
    margin: 0px;
    color: white;
    text-decoration: none;
}

@media(max-width:970px){
    .Asec2A2{
        grid-template-columns: 1fr;
    }
    .Asec2A3{
        padding: 5px 20px;
    }
    .Asec2A4{
        height: auto;
        padding-left: 30px;
    }
}
@media(max-width:550px){
    .Asec2A4{
        height: auto;
        padding-left: 22px;
    }
    .Asec2A3{
        height: 350px;
        padding: 15px 0;
    }
    .Asec2h3{
        font-size:25px;
    }
    .Asec2p1{
        font-size: 15px;
        
    }
}




/* .........About sec 3 feedback css............... */

.Aboutsec3a{
    display: flex;
    align-items:center;
    justify-content:center;
    border: 1px solid black;
    background: rgba(0, 0, 0, 0.6) url('../../assets/Aboutimg/feedbackbg-1.jpg') no-repeat center;
    background-size: cover;
    background-blend-mode: darken;
    padding: 45px 30px;

}
.feedbacksec1{
    display: flex;
    align-items:center;
    justify-content:center;
    width: 100%;
    flex-direction: column;
    gap: 20px;
    padding: 20px 10px;
}
.FBtitleh6{
    margin: 0;
    font-size: 20px;
    color: #c19977;
    letter-spacing: 1.5px;
    font-weight: 600;
}
.FBtitleh3{
    font-family: 'Times New Roman', Times, serif;
    margin: 0;
    font-size: 40px;
    color:white;
    letter-spacing: 1.5px;
    font-weight: 600;
}

.Fbslide{
    width: 100%;
}
.Fb1{
    margin: auto;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 60px;
}
.fbtexth5{
    color: white;
    text-align:center;
    font-weight: 500;
    font-size: 20px;
    font-family:'Times New Roman', Times, serif;
}
.Aboutsec3a .react-multiple-carousel__arrow{
    display: none;
}
.Fbline1{
    color: white;
    margin: 0;
    font-size: 10px;
    font-weight: 700;
}
.Fbname1{
    color: #c29977;
    font-size: 15px;
    font-weight: 500;
    letter-spacing: 0.5px;
}

@media(max-width:750px) {
    .Fb1{
        width:90%;
        padding:30px 40px;
    }
    .fbtexth5{
        font-size: 18px;
    }
    .FBtitleh3{
        font-size: 30px;
    }
}
@media(max-width:500px) {
    .Fb1{
        width:95%;
        padding:40px 35px;
    }
    .fbtexth5{
        font-size: 18px;
    }
    .FBtitleh3{
        font-size: 28px;
    }
}